import { InjectionKey } from "vue";
import { createStore, useStore as baseUseStore, Store } from "vuex";
import { Note } from "@/lib/types";
import NoteManager from "./lib/NoteManager";

// export interface Book {
//   name: string;
//   author: string;
// }

// „Çπ„Éà„Ç¢„ÅÆÂûã„ÇíÂÆöÁæ©„Åô„Çã
export interface State {
  currentNoteName: Note;
}

// InjectionKey„ÇíÂÆöÁæ©„Åô„Çã
export const key: InjectionKey<Store<State>> = Symbol();

// Store„Çí‰ΩúÊàê„Åô„Çã
export const store = createStore<State>({
  state: {
    currentNoteName: "C4",
  },
  getters: {
    getCurrentNoteName: (state) => {
      // return state.books ? state.books[0] : ({} as Book);
      return state.currentNoteName;
    },
  },
  mutations: {
    // changeNote(state, { noteName }) {
    //   state.currentNoteName = noteName;
    // },
    changeNote(state) {
      const note = NoteManager.generateNote();
      console.log(note);
      state.currentNoteName = note;
      // state.currentNoteName = note;
    },
  },
});

// Áã¨Ëá™„ÅÆuserStore„É°„ÇΩ„ÉÉ„Éâ„ÇíÂÆöÁæ©„Åô„Çã
export function useStore() {
  // InjectionKey„ÇíuseStore„É°„ÇΩ„ÉÉ„Éâ„Å´Ê∏°„Åô
  return baseUseStore(key);
}
